.activity-item {
    position: relative;
    padding: 4.0rem 3.2rem;
    width: 100%;
    aspect-ratio: 379 / 298;
    border: 1px solid #E3D9CA;

    .title {
        color: $green_color;
        margin-bottom: 1.6rem;
        line-height: 2.4rem;
    }

    .content {
        margin-bottom: 2.0rem;
        line-height: 2.2rem;
        display: -webkit-box;
        -webkit-line-clamp: 3;
        /* Number of lines to display */
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .company-province {
        margin-bottom: 2.0rem;
    }
}

.activity-item.new::after {
    position: absolute;
    top: 0;
    right: 0;
    width: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    content: "NEW";
    padding: 0.5rem 1.6rem;
    background: #CEBA6E;
    color: #FFF;
    font-family: "Montserrat";
    font-size: 1.2rem;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 108.333% */
}

.activity-item:hover {
    opacity: 0.7;
}

.categories {
    display: flex;
    column-gap: 0.8rem;
}

.news {
    .head-title {
        margin-bottom: 3.2rem;
        color: $green_color;
    }

    .news-list {
        .news-item {
            position: relative;
            width: 100%;
            padding: 1.6rem 0;
            border-bottom: 1px solid #E3D9CA;

            a {
                width: 100%;
                position: relative;
                display: flex;
                align-items: center;

                &:hover {
                    opacity: 0.7;
                }
            }

            .date {
                width: 12%;
                flex-shrink: 0;
            }

            .categories {
                width: 13.6%;
                flex-shrink: 0;
            }

            .title {
                width: 75%;
                color: $green_color;
            }
        }
    }
}

.link-wrapper {
    display: flex;
