}

/* custom checkbox */
input[type=checkbox] {
    position: relative;
    border-radius: 4px !important;
    border: 1px solid #CEA76E !important;
    cursor: pointer;
    line-height: 0;
    outline: 0;
    padding: 0 !important;
    vertical-align: text-top;
    height: 2.0rem !important;
    width: 2.0rem !important;
    -webkit-appearance: none;
    opacity: 1;
    background: #FFF;
    margin: 0 !important;
}

input[type=checkbox]:hover {
    opacity: 0.7;
}

input[type=checkbox]:checked {
    background-color: $green_color;
    border: 1px solid $green_color !important;
    opacity: 1;
}

input[type=checkbox]:before {
    content: '';
    position: absolute;
    right: calc(50% + 0rem);
    top: 50%;
    width: 0.5rem;
    height: 1rem;
    border: solid #FFF;
    border-width: 0 2px 2px 0;
    margin: -1px -1px 0 -1px;
    transform: rotate(45deg) translate(-50%, -50%);
    z-index: 2;
}

input[type=checkbox]:indeterminate {
    background: $green_color;
}

input[type=checkbox]:indeterminate:before {
    border-width: 0;
    transform: unset;
    width: 6px;
    height: 6px;
    top: 5px;
    left: 5px;
    background: #FFF;
}

.breadcrump {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    column-gap: 0.8rem;
    background: #CEBA6E;
    padding: 1.0rem 5.6rem;
    height: 3.6rem;
    color: $green_color;
    font-family: "Higure Gothic";
    font-size: 1.3rem;
    font-style: normal;
    font-weight: 500;
    line-height: 13px; /* 100% */
    margin-bottom: 7.2rem;
    .home {
        img {
            width: 1.6rem;
            height: 1.6rem;
        }
    }
    .link {
        font-weight: 500;
        white-space: nowrap;
        &:hover {
            opacity: 0.7;
        }
    }
    .right-symbol {
        width: 1.6rem;
        height: 1.6rem;
    }
    .current-page {
        white-space: nowrap;
        font-weight: 400;
    }
}